/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */
html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif; }

.demo-avatar {
  width: 48px;
  height: 48px;
  border-radius: 24px; }

.demo-layout .mdl-layout__header .mdl-layout__drawer-button {
  color: rgba(0, 0, 0, 0.54); }

.mdl-layout__drawer .avatar {
  margin-bottom: 16px; }

.demo-drawer {
  border: none; }

/* iOS Safari specific workaround */
.demo-drawer .mdl-menu__container {
  z-index: -1; }

.demo-drawer .demo-navigation {
  z-index: -2; }

/* END iOS Safari specific workaround */
.demo-drawer .mdl-menu .mdl-menu__item {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center; }

.demo-drawer-header {
  box-sizing: border-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: flex-end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  padding: 16px;
  height: 151px;
  color: rgba(255, 255, 255, 0.77); }

.demo-avatar-dropdown {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  width: 100%; }

.demo-navigation {
  -webkit-flex-grow: 1;
  -ms-flex-positive: 1;
  flex-grow: 1; }

.demo-layout .demo-navigation .mdl-navigation__link {
  display: -webkit-flex !important;
  display: -ms-flexbox !important;
  display: flex !important;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.77);
  font-weight: 500; }

.demo-layout .demo-navigation .mdl-navigation__link:hover {
  background-color: #ff5722;
  color: #FFFFFF; }

.demo-navigation .mdl-navigation__link .material-icons {
  font-size: 24px;
  color: rgba(255, 255, 255, 0.77);
  margin-right: 32px; }

main .tab {
  display: none; }

main .tab.active {
  display: block; }

#view-source {
  position: fixed;
  display: block;
  right: 0;
  bottom: 0;
  margin-right: 40px;
  margin-bottom: 40px;
  z-index: 900; }

#appView {
  width: 100%; }

.title {
  text-align: center;
  letter-spacing: .1rem; }

.title i {
  font-size: 4rem; }

.inputForm {
  text-align: center; }

.center {
  text-align: center;
  margin: 0 auto;
  display: block; }

.vennSearchView .searchContainer {
  margin: 0 auto; }

.vennSearchView .primaryResultsContainer .wordInputContainer {
  max-width: 1300px; }

.vennSearchView .primaryResultsContainer .wordSelection {
  margin: 0 auto;
  text-align: center;
  display: block; }

#treeChartView .node.topic.active text {
  font-size: 1.5rem; }

#treeChartView .node.topic.active circle {
  fill: #1a237e;
  stroke: #ff5722;
  stroke-width: 3px; }

#treeChartView .node circle {
  fill: #fff; }

#treeChartView .node text {
  fill: white;
  font-size: 1.3rem; }

#treeChartView .node--internal text {
  fill: white;
  font-size: 1.3rem; }

#treeChartView .link {
  fill: none;
  stroke: #ff5722;
  stroke-width: 4px; }

.docFound i.material-icons {
  padding-top: 3px;
  color: white; }

.searchedTerms i.material-icons {
  padding-top: 3px;
  color: white; }

.graphs {
  /*height:600px;*/
  text-align: center;
  overflow: hidden; }

@media (max-width: 839px) and (min-width: 480px) {
  .searchContainer .searchRow {
    margin: 0 auto; } }

.vennCircle1 {
  fill: green;
  text-anchor: "middle";
  fill-opacity: .20; }

.vennCircle2 {
  text-anchor: "middle";
  fill: blue;
  fill-opacity: .20; }

.ct-series-a .ct-point, .ct-series-a .ct-line, .ct-series-a .ct-bar, .ct-series-a .ct-slice-donut {
  stroke: green; }

.ct-series-a .ct-slice-pie, .ct-series-a .ct-area {
  fill: green; }

.ct-series-b .ct-point, .ct-series-b .ct-line, .ct-series-b .ct-bar, .ct-series-b .ct-slice-donut {
  stroke: blue; }

.ct-series-b .ct-slice-pie, .ct-series-b .ct-area {
  fill: blue; }

.ct-series-c .ct-point, .ct-series-c .ct-line, .ct-series-c .ct-bar, .ct-series-c .ct-slice-donut {
  stroke: red; }

.ct-series-c .ct-slice-pie, .ct-series-c .ct-area {
  fill: red; }

#home .search1Legend {
  color: green; }

#home .search2Legend {
  color: red; }

#home .search3Legend {
  color: blue; }
